body {
  background-color: rgb(22, 22, 22);
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  color: white;
}

.homeLayout {
  display: flex;
  flex-direction: column;
  align-content: center;
}

.container {
  min-width: 230px;
  display: flex;
  overflow: hidden;
}

.movie {
  margin-right: 10px;
}

.noobflix {
  position: fixed;
  top: 650px;
  left: 800px;
  color: red;
  font-size: 100px;
  font-family: "Lucida Sans", "Lucida Sans Regular", "Lucida Grande",
    "Lucida Sans Unicode", Geneva, Verdana, sans-serif;
}

.genre-list {
  list-style: none;
  padding-left: 0;
}

.container {
  margin-top: 50px;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
}

.background {
  background-size: 1600px 800px;
}

p {
  width: 1000px;
}

ul {
  list-style: none;
  padding-left: 0;
}

